nav{
    display: flex;
    justify-content: space-between;
    padding: 15px 4.4vw;
}
.blogo{
    /* width: 100px; */
    /* height: 100px; */
    object-fit: cover;
}
.blogo img{
    object-fit: cover;
    width: 76px;
    /* height: 42.75px; */
}
ul{
    display: flex;
    padding: 0;
    gap: 2vw;
}
.ul{
    justify-content: center;
    align-items: center;
    display: flex;
}
li{
    list-style: none;
}
li a{
    font-weight: 600;
    font-size: 1.2vw;
    text-decoration: none;
    color: white;
    transition: all ease .5s;
}
li a:hover{
    text-shadow: 
      0 0 5px lightskyblue, 
      0 0 10px lightskyblue, 
      0 0 20px lightskyblue, 
      0 0 30px lightskyblue, 
      0 0 40px lightskyblue, 
      0 0 50px lightskyblue, 
      0 0 60px lightskyblue;
      transform: scale(1.1);
}
nav button{
    padding: 5px 20px;
    width: 6vw;
    height: 3vw;
    background-color: #D01C28;
    /* font-family: ; */
    color: white;
    font-weight: 600;
    font-size: 1vw;
}

@media screen and (max-width: 1000px){
    nav{
        padding: 15px 4vw;
        display: flex;
       justify-content: center;
       justify-content: space-between;
       align-items: center;
    }
    li a{
        font-weight: 600;
        font-size: 1.6vw;
        text-decoration: none;
        color: white;
    }
    .ul{
        left: 0;
    }
     button{
        /* padding: 5px 20px; */
        width: 150px;
        height: 40px;
        background-color: #D01C28;
        /* font-family: ; */
        color: white;
        display: flex;
        justify-content: center;
        font-weight: 600;
        font-size: 1vw;
    }
}

@media screen and (max-width: 800px){
    ul{
        justify-content: center;
        align-items: center;
        display: flex;
    }
    li a{
        font-weight: 600;
        font-size: 2.2vw;
        text-decoration: none;
        color: white;
    }
    nav button{
        /* padding: 5px 20px; */
        width: 70px;
        height: 30px;
        background-color: #D01C28;
        /* font-family: ; */
        color: white;
        font-weight: 600;
        font-size: 2vw;
    }
}

@media screen and (max-width: 500px){
    nav{
    padding: 15px 4vw;
    display: flex;
    justify-content: center;
    justify-content: space-between;
    align-items: center;
    }
    nav button{
        /* padding: 5px 20px; */
        width: 70px;
        height: 30px;
        background-color: #D01C28;
        /* font-family: ; */
        color: white;
        font-weight: 600;
        font-size: 3vw;
    }
    .ul{
        width: fit-content;
        height: fit-content;
    }
}