.midmain{
    padding: 10px 17vw;
    /* background-color: #f0f0f0; */
display: flex;
justify-content: space-between;
/* overflow-x: hidden; */
/* flex-wrap: wrap; */
}
.nme{
    font-size: 1vw;
    text-transform: capitalize;
}
h1{
    font-weight: 800;
    font-size: 5.6vw;
    line-height: .5;
}
.deserve {
    color: white;
    font-size: 3em; 
    text-align: center;
    text-shadow: 
      0 0 5px red, 
      0 0 10px red, 
      0 0 20px red, 
      0 0 30px red, 
      0 0 40px red, 
      0 0 50px red, 
      0 0 60px red;
    font-family: 'Poppins', sans-serif;
    font-weight: 800;
    left: 0;
    text-align: left;
    font-size: 5.6vw;
    line-height: .5;
  }
h3{
    font-weight: 600;
    font-size: 16px;
    color: white;
}
.btns{
    display: flex;
    gap: 3vw;
}
.shopbtn{
    font-size: 24px;
    font-weight: 600;
    background-color: #D01C28;
    color: white;
    padding: 10px;
}
.category{
    font-size: 24px;
    border: 2px solid black;
    background-color: #fff;
    color: gray; padding: 10px;
}
.mini{
    width: 32px;
    width: 32px;
}
.mini img{
    width: 40px;
    width: 40px;
}
.miniimgs{
    display: flex;
    gap: 2vw;
}
.down{
    display: flex;
    flex-direction: column;
}
.midright{
    display: flex;
    padding-top: 50px;
    
}
.midright img{
    width: 31.25vw;
    height: 31.25vw;
    
    
    /* box-shadow: 32 32 ; */
}

@media screen and (max-width: 1000px) {
    .midmain{
        padding: 10px 12vw;
        /* background-color: #f0f0f0; */
        display: flex;
        justify-content: space-between;
        /* flex-wrap: wrap; */
    }
    .nme{
        font-size: 2vw;
        text-transform: capitalize;
    }
    h1{
        font-weight: 700;
        font-size: 5.5vw;
        line-height: .3;
    }
    
    h3{
        font-weight: 600;
        font-size: 14px;
        color: white;
    }
    .btns{
        width: 50%;
        display: flex;
        flex-direction: column;
        gap: 1vw;
    }
    .shopbtn{
        font-size: 18px;
        font-weight: 600;
        background-color: #D01C28;
        color: white;
        padding: 10px;
    }
    .category{
        font-size: 18px;
        border: 2px solid black;
        background-color: #fff;
        color: gray; padding: 10px;
    }
    .midright img{
        width: 38vw;
        height: 38vw;
        /* box-shadow: 32 32 ; */
    }
}


@media screen and (max-width: 800px){
    .midmain{
        padding: 10px 5vw;
        /* background-color: #f0f0f0; */
        display: flex;
        /* justify-content: space-between; */
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .nme{
        font-size: 2vw;
        text-transform: capitalize;
    }
    .deserve {
        color: white;
        font-size: 3em; /* Adjust the font size as needed */
        text-align: center;
        text-shadow: 
          0 0 5px red, 
          0 0 10px red, 
          0 0 20px red, 
          0 0 30px red, 
          0 0 40px red, 
          0 0 50px red, 
          0 0 60px red;
        font-family: 'Poppins', sans-serif;
        font-weight: 800;
        left: 0;
        text-align: center;
        font-size: 5.6vw;
        line-height: .5;
      }
    .midleft{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 70vw;
    }
    h1{
        font-weight: 700;
        text-align: center;
        font-size: 7vw;
        line-height: .3;
    }
    h3{
        text-align: center;
    }
    .btns{
        display: flex;
        justify-content: center;
        align-items: center;
       
        /* flex-direction: column; */
        gap: 1vw;
    }
    .miniimgs{
        display: flex;
        /* gap: 2vw; */
        justify-content: space-between;
    }
}

@media screen and (max-width: 500px) {
    h1{
        font-weight: 700;
        font-size: 10vw;
        line-height: .3;
    }
    h3{
        font-weight: 600;
        text-align: center;
        font-size: 2.5vw;
        color: white;
    }
    .midright img{
        width: 70vw;
        height: 70vw;
        /* box-shadow: 32 32 ; */
    }
    .midright{
        display: flex;
        padding-top: 10px;
    }
}